<template>
  <div>
    <div class="home-nav" @click="goPosition">
      <van-icon name="location-o" />
      {{ pCity }}
      <van-icon name="arrow" />
    </div>
    <div id="container"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cinemaList: [],
    }
  },
  computed: {
    pCity() {
      return this.$store.state.geolocation.positionCity || '正在定位'
    },
  },

  mounted() {
    // 创建地图实例
    const map = new BMap.Map('container')
    // 创建点坐标
    // const point = new BMap.Point(116.404, 39.915)
    // 初始化地图，设置中心点坐标和地图级别
    // map.centerAndZoom(point, 15)
    //在地图上显示附近的影院
    // const local = new BMap.LocalSearch(map, {
    //   renderOptions: { map: map },
    // })
    // local.search('影院')
    // 还可以输入关键字
    // map.centerAndZoom('上海', 12)
    //开启鼠标滚轮缩放
    map.enableScrollWheelZoom(true)
    //定位到当前城市
    const geolocation = new BMap.Geolocation()
    geolocation.getCurrentPosition(
      function (r) {
        //关于状态码
        //BMAP_STATUS_SUCCESS检索成功对应数值0
        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
          // console.log(this);//不是箭头函数指向geolocation对象
          //定位信息
          // console.log(r);
          //添加标记
          const mk = new BMap.Marker(r.point)
          map.addOverlay(mk)
          //经度r.point.lng  纬度r.point.lat
          //将让地图平滑移动至新中心点(经纬度对象)
          //需要创建点坐标设置中心点坐标
          //map.panTo(r.point)
          //或者创建点坐标设置中心点坐标
          const point = new BMap.Point(r.point.lng, r.point.lat)
          map.centerAndZoom(point, 15)
        } else {
          console.log('failed' + this.getStatus())
        } //启用高精度
      },
      { enableHighAccuracy: true }
    )
    // 添加带有定位的导航控件
    const navigationControl = new BMap.NavigationControl({
      // 靠左上角位置
      anchor: BMAP_ANCHOR_TOP_LEFT,
      // LARGE类型
      type: BMAP_NAVIGATION_CONTROL_LARGE,
      // 启用显示定位
      enableGeolocation: true,
    })
    map.addControl(navigationControl)
    // 添加定位控件
    // const geolocationControl = new BMap.GeolocationControl()
    // map.addControl(geolocationControl)
    //获取附近影院信息
    const options = {
      onSearchComplete(res) {
        if (local.getStatus() == BMAP_STATUS_SUCCESS) {
          // console.log(res)
          // this.cinemaList = res.Ir
        }
      },
    }
    const local = new BMap.LocalSearch(map, options)
    local.search('影院')
    // 计算距离map.getDistance(pointA, pointB)
    // map.centerAndZoom("重庆", 12);  //初始化地图,设置城市和地图级别
    // const pointA = new BMap.Point(106.486654, 29.490295);  // 创建点坐标A
    // const pointB = new BMap.Point(106.581515, 29.615467);  // 创建点坐标B
    // console.log('从A到B的距离是：' + (map.getDistance(pointA, pointB)).toFixed(2) + '米');  //获取两点距离,保留小数点后两位
    // const polyline = new BMap.Polyline([pointA, pointB], { strokeColor: "blue", strokeWeight: 6, strokeOpacity: 0.5 });  //定义折线
    // map.addOverlay(polyline); //添加折线到地图上
  },
  methods: {
    goPosition() {
      this.$router.push('/position')
    },
  },
}
</script>
<style scoped>
.home-nav {
  width: 100%;
  color: #fff;
  font-size: 35px;
  padding: 10px;
}
#container {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
}
</style>